import React from 'react';
import Category from './category';
const axios = require('axios');
var dateFormat = require('dateformat');

class App1 extends React.Component {
  constructor(props)
  {
    super(props);
    this.state={
      nList:0,
      data:[],
      amount:0,
      isCOP: false,
      isCOD: false,
      isOnline: false,
      tempAmt:0,
      tempId:'',tempRefno:'',
      isRefresh:false,isNew:false,tempOTP:''
    }
  }

  orderlist(cond)
  {
    axios.get(global.serverURL + "/api/courier/myorders", {
      params: {
        "mobno": global.mobno || "9095204586"
      }
    })
    .then((response) => {
      console.log(response)
      return response.data.data;
    })
    .then(data => {
      this.setState({ nList: data.length, data:data, isRefresh:!this.state.isRefresh });
    }).catch(error => {
      this.setState({ nList: 0 });      
      console.log(error);
    });
  }

  onlinePay=(amt,id,otp)=>
  {
    this.setState({tempAmt:amt.amt1,tempId:id.id,tempOTP:otp.otp});
  }

  componentDidMount()
  {
    this.orderlist();
  }

  clickType=(type,res)=>{
    if(type==='cancelbooking')
    {
      let conf=window.confirm('Are u want sure to Cancel Booking?');
      if(conf) 
      {
        console.log(res.id)
        axios.post(global.serverURL + "/api/driver/BookingAction",  {
          "id": res.id1,"action":"reject"
        }
        )
        .then((response) => {
          console.log(response)
          if(response.status===200)
          {
            this.orderlist();
          }
          return response.data;
        }).catch(error => {
          console.log(error);
        });
      }
      else alert('false');
    }
    else if(type==='online')
    {
      this.openCheckout();
    }
    else if(type==='new')
    {
      this.setState({isNew:true});
    }
  }

  openCheckout=()=> {
    let id1=this.state.tempId;
    let options = {
      "key": "rzp_test_zfgh53To0jzwp9",
      "amount": this.state.tempAmt*100, // 2000 paise = INR 20, amount in paisa
      "name": "Donkeycargo Pvt Ltd.,",
      "description": "Online Shipping System",
      "image": "./dist/img/DonkeyUser.png",
      "handler": function (response) {
        alert(response.razorpay_payment_id);
        
        axios.post(global.serverURL + "/api/courier/updatePaymode",  {
          "id": id1,"paymode":"online","refno":response.razorpay_payment_id
        }
        )
        .then((response) => {
          return response.data.data;
        }).catch(error => {
          console.log(error);
        });

      },
      "prefill": {
        "name": "Karthic",
        "email": "karthi@gmail.com"
      },
      "notes": {
        "address": "Hello World"
      },
      "theme": {
        "color": "#F37254"
      }
    };

    let rzp = new window.Razorpay(options);
    rzp.open();
  }

  render()
  {
    let res;
    const buttonstylefocus={
      textAlign: 'center',
      display: 'inline-block',
      padding: '13px 20px',
      borderRadius: '10px',
      fontSize: '16px',
      letterSpacing: '1px',
      textTransform: 'uppercase',
      fontWeight: '700',
      border: '1px solid #f4511e',
      color: '#fff',
      background: '#f4511e',
      boxShadow: '0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 20px 29px 0 rgba(0, 0, 0, 0.19)'
    };
    const buttonstyle={
      textAlign: 'center',
      display: 'inline-block',
      padding: '13px 20px',
      borderRadius: '10px',
      color: '#f4511e',
      fontSize: '16px',
      letterSpacing: '1px',
      textTransform: 'uppercase',
      border: '1px solid #f4511e',
      fontWeight: '700',
      textDecoration:'none',
      background: 'transparent'
    };
    const styleForm={
      paddingLeft:'10px',
      paddingBottom:'5px'
    }
    let orders=[]
    let i=0;
    for(i=0;i<this.state.nList;i++)
    {
      let booktime=dateFormat(this.state.data[i].BookDate, "dddd, mmm dS, yyyy h:MM:ss TT",true);
      let amt1=this.state.data[i].NetTotal
      let id1=this.state.data[i].id
      let id=this.state.data[i].BookingId
      let otp=this.state.data[i].otp
      orders.push(

        <div class="card card-default">
        <div class="card-body">
        <div class="callout callout-danger">
        <div className="row">
        <div className="col-9">
        <h3>Order #{this.state.data[i].BookingId}</h3>    
        </div>
        <div className="col-3" >
        {this.state.data[i].Refno===''?<button className="btn btn-sm btn-danger" readOnly>Unpaid</button>:<button className="btn btn-sm btn-success" readOnly>Paid</button>} 
        </div>  

        <div className="col-12 mb-2">
        <p>Placed on {booktime}</p>
        </div>             

        <div className="col-12">
        <div style={styleForm}>
        <label className="select2text">From</label>
        <div className="input-group">
        <span class="input-group-text text-success"><i class="fa fa-dot-circle-o"></i></span>
        <div className="input-group-prepend">
        </div>
        <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.data[i].FromAddress} />
        </div>
        </div>
        </div>

        <div className="col-12">
        <div style={styleForm}>
        <label className="select2text">To</label>
        <div className="input-group">
        <span class="input-group-text text-danger"><i class="fa fa-dot-circle-o"></i></span>
        <div className="input-group-prepend">
        </div>
        <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.data[i].toaddress} />
        </div>
        </div>
        </div>

        <div className="col-7">
        <div style={styleForm}>
        <label className="select2text">Courier Type</label>
        <div className="input-group mb-3">
        <span class="input-group-text text-primary"><i class="fa fa-envelope-o"></i></span>
        <div className="input-group-prepend">
        </div>
        <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.data[i].Type} />
        </div>
        </div>
        </div>
        <div className="col-5 mb-2">
        <div style={styleForm}>
        <label className="select2text">Amount</label>
        <div className="input-group mb-3">
        <span class="input-group-text bg-danger"><i class="fa fa-inr"></i></span>
        <div className="input-group-prepend">
        </div>
        <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.data[i].NetTotal} />
        </div>
        </div>
        </div>


        <div className="col-6 col-md-3 mb-2 text-center">
        <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link" onClick={()=>this.clickType('cancelbooking',{id1})}>Cancel Booking</a>
        </div>
        <div className="col-6 col-md-3 text-center">
        <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link" data-toggle="modal" data-target="#myModal2" onClick={()=>this.onlinePay({amt1},{id})}>Setup Payment</a>
        </div>
        <div className="col-6 col-md-3 text-center">
        <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link" data-toggle="modal" data-target="#myModal3" onClick={()=>this.onlinePay({amt1},{id},{otp})}>Preview OTP</a>
        </div>
        <div className="col-6 col-md-3 text-center">
        <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link">View Status</a>
        </div>

        </div>

        </div>

        </div>
        </div>
        );
      }

      res = <div className="wrapper">
      <div className="">
      <div className="row">
      <div className="col-12 text-center">

      <div className="info-box bg-danger">
      <span className="info-box-icon"><i className="fa fa-shopping-cart"></i></span>

      <div className="info-box-content">
      <span className="info-box-text" style={{fontSize:'26px'}}>My Orders</span>
      <div className="progress">
      <div className="progress-bar" style={{width: '100%'}}></div>
      </div>
      <span className="progress-description">You have {this.state.nList} New Order(s). </span>
      </div>
      </div>
      </div>

      <div className="col-12">
      {orders}        

      </div>

      <div className="col-12 mt-3">
      <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstylefocus} className="col-12 orange-back-link" onClick={()=>this.clickType('new')}>New Booking</a>
      </div>

      </div>
      </div>


      <div id="myModal2" className="modal fade" role="dialog">
      <div className="modal-dialog  modal-dialog-centered">

      <div className="modal-content">
      <div className="modal-header">
      <h4 className="modal-title">Setup Payment</h4>
      <button type="button" className="close" data-dismiss="modal">&times;</button>
      </div>
      <div className="modal-body">
      <div className="row">
      <div className="col-12">
      <a href="#" style={this.state.isCOP ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("cop")} data-dismiss="modal">
      <span style={{ display: 'grid' }}><i className="fa fa-truck fa-3x"></i>
      Cash on Pickup</span></a>
      </div>

      <div className="col-12">
      <a href="#" style={this.state.isCOD ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("cod")} data-dismiss="modal">
      <span style={{ display: 'grid' }}><i className="fa fa-truck fa-flip-horizontal fa-3x"></i>
      Cash on Delivery</span></a>
      </div>

      <div className="col-12">
      <a href="#" style={this.state.isOnline ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("online")}>
      <span style={{ display: 'grid' }} data-dismiss="modal"><i className="fa fa-credit-card fa-3x"></i>
      Online Payment</span></a>
      </div>

      </div>
      <div className="modal-footer">
      <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
      </div>
      </div>

      </div>
      </div>
      </div>

      <div id="myModal3" className="modal fade" role="dialog">
      <div className="modal-dialog  modal-dialog-centered">

      <div className="modal-content">
      <div className="modal-header">
      <h4 className="modal-title">Pickup OTP</h4>
      <button type="button" className="close" data-dismiss="modal">&times;</button>
      </div>
      <div className="modal-body">
      <div className="row">
      <div className="col-12 text-center mb-2">
      Pickup OTP is <span class="bg-danger">{this.state.tempOTP}</span>
      </div>
      </div>
      <div className="modal-footer">
      <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
      </div>
      </div>

      </div>
      </div>
      </div>

      </div>;

      if(this.state.isNew)
      {
        return <Category />
      }
      else    return (res);
    }
  }

  export default App1;
